@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "PTMono";
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Roboto Condensed";
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "PT Mono", "Arial", sans-serif;
  color: #041827;
  background-color: #f8fdff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.main {
  margin-top: 102px;
}
@media (max-width: 1023px) {
  .main {
    margin-top: 85px;
  }
}
@media (max-width: 767px) {
  .main {
    margin-top: 60px;
  }
}

.list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.link {
  text-decoration: none;
}

.button {
  width: fit-content;
  height: fit-content;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background: transparent;
  cursor: pointer;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1303px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 50px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 26px;
  }
}

.header {
  position: fixed;
  display: flex;
  min-height: 120px;
  width: 100%;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
  z-index: 1;
}

@media (max-width: 1023px) {
  .header {
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
  }
}
.nav {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  transform: translate(0, 30px);
}

.nav__logo--picture img {
  transition: all 0.5s ease-in-out;
}

.nav__logo--picture img:hover,
.nav__logo--picture img:focus {
  opacity: 0.8;
  transform: scale3d(1.02, 1.02, 1.02);
}

.nav__logo--picture img:active {
  opacity: 0.6;
  transform: none;
}

.nav__list {
  display: flex;
  transform: translate(3.6vw, 15px);
  gap: 6.25vw;
  flex-wrap: wrap;
}

.nav__item--link {
  display: flex;
  align-items: center;
  gap: 15px;
  font-family: "PT Mono", "Courier New", monospace;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #f2f5f7;
  transition: all 0.5s ease-in-out;
}

.nav__item--link:hover,
.nav__item--link:focus {
  opacity: 0.8;
  transform: scale3d(1.02, 1.02, 1.02);
}

.nav__item--link:active,
.nav__item--link:active .nav__item--link svg {
  color: rgba(248, 253, 255, 0.7);
  transform: none;
}

.nav__item--link svg {
  color: #00b2ff;
}

.nav__button {
  margin-left: auto;
  transform: translate(-63px, 5px);
  padding: 9.5px 17px 13px 16px;
  color: #f2f5f7;
  border: 1px solid #f2f5f7;
  border-radius: 3px;
  transition: all 0.5s ease-in-out;
}

.nav__button:hover,
.nav__button:focus {
  background: #00b2ff;
  border: 1px solid #00b2ff;
}

.nav__button:active {
  opacity: 0.8;
  transform: translate(-61px, 5px) scale3d(1.02, 1.02, 1.02);
}

@media (max-width: 1023px) {
  .header__nav {
    grid-template-columns: 1fr 2fr;
    transform: translate(0, 20px);
  }
  .nav__list {
    margin-left: auto;
    transform: translate(-29px, 11px);
    gap: 5.35vw;
  }
  .nav__item--text,
.nav__button {
    display: none;
  }
}
@media (max-width: 767px) {
  .header__nav {
    transform: translate(0, 16px);
  }
  .nav__logo--picture img {
    width: 35px;
    height: auto;
  }
  .nav__list {
    transform: translate(-20px, 4px);
    gap: 11.4vw;
  }
}
.printed {
  position: relative;
  height: 778px;
}

.printed__picture img {
  width: 100vw;
  height: 778px;
  position: absolute;
  background-color: #041827;
}

.printed__container {
  position: relative;
  padding-top: 142px;
  padding-left: 164.5px;
}
.printed__container h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 80px;
  line-height: 80px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  color: #f2f5f7;
}
.printed__container p {
  width: 60%;
  margin-bottom: 18px;
  font-family: inherit;
  font-weight: 400;
  font-size: 22px;
  line-height: 35px;
  letter-spacing: 1.85px;
  color: #f2f5f7;
  transform: translate(0, -27px);
}

.printed__button {
  display: block;
  padding: 16px 26px 16.5px;
  font-size: 18px;
  line-height: 21px;
  color: #f2f5f7;
  background: #00b2ff;
  border-radius: 3px;
  border: 1px solid #00b2ff;
  transition: all 0.5s ease-in-out;
}

.printed__button:hover,
.printed__button:focus {
  background: #041827;
  border: 1px solid #f2f5f7;
}

.printed__button:active {
  background: #00b2ff;
  border: 1px solid #00b2ff;
  opacity: 0.8;
  transform: scale3d(1.02, 1.02, 1.02);
}

.printed__button--span-inner {
  display: none;
}

@media (max-width: 1023px) {
  .printed {
    height: 555px;
  }
  .printed__picture img {
    height: 555px;
  }
  .printed__container {
    margin-left: 0;
    padding-top: 43px;
  }
  .printed__container h2 {
    font-size: 65px;
    line-height: 70px;
    letter-spacing: 0.06em;
  }
  .printed__container p {
    width: 100%;
    margin-bottom: 4px;
    transform: translate(0, -51px);
  }
  .printed__button {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .printed {
    height: 500px;
  }
  .printed__picture img {
    height: 500px;
  }
  .printed__container {
    padding-top: 60px;
  }
  .printed__container h2 {
    font-size: 36px;
    line-height: 42px;
    letter-spacing: 0.07em;
  }
  .printed__container p {
    margin-bottom: 23px;
    font-size: 14px;
    line-height: 22px;
    letter-spacing: 1.1px;
    transform: translate(-2px, -22px);
  }
  .printed__button {
    width: auto;
    margin-left: 0;
    padding: 11px 26px 11px;
    font-size: 14px;
    line-height: 16px;
    transform: translate(-1px, 0);
  }
  .printed__button--span {
    display: none;
  }
  .printed__button--span-inner {
    display: block;
  }
}
.advantages {
  padding-top: 120px;
  padding-bottom: 80px;
}

.advantages__list {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px 31px;
}

.advantages__item {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  min-height: 300px;
  padding: 41px 41px 34px;
  border-left: 5px solid #00b2ff;
  transition: all 0.5s ease-in-out;
}

.advantages__item:hover,
.advantages__item:focus,
.advantages__item:active {
  background-color: #00b2ff;
}

.advantages__item svg {
  color: #00b2ff;
  transition: all 0.5s ease-in-out;
}

.detective__item svg {
  transform: translate(-5px, -1px);
}

.advantages__item:hover svg,
.advantages__item:focus svg {
  color: #f2f5f7;
}

.advantages__item:active svg {
  color: rgba(248, 253, 255, 0.7);
}

.advantages__item h3 {
  margin: 20px 0 10px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  color: #00b2ff;
  transition: all 0.5s ease-in-out;
}

.advantages__item:hover h3,
.advantages__item:focus h3,
.advantages__item:active h3 {
  color: #f2f5f7;
}

.advantages__item p {
  margin: 3px 0 18px;
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  color: #041827;
  line-height: 21.8px;
  letter-spacing: 1.35px;
  transition: all 0.5s ease-in-out;
}

.advantages__item:hover p,
.advantages__item:focus p,
.advantages__item:active p {
  color: #f2f5f7;
}

@media (max-width: 1023px) {
  .advantages {
    padding-top: 80px;
  }
  .advantages__list {
    gap: 30px 28px;
  }
  .advantages__item {
    min-height: 360px;
    padding: 41px 41px 17px;
  }
  .detective__item svg {
    transform: translate(-1px, -1px);
  }
  .tag__item svg {
    transform: translate(2px, -1px);
  }
  .advantages__item h3 {
    margin: 16px 0 7px;
  }
  .detective__item h3 {
    margin: 13px 0 6px;
  }
  .tag__item h3 {
    margin: 10px 0 4px;
  }
  .advantages__item p {
    margin: 0 0 16px;
    font-size: 16px;
    line-height: 21.6px;
    letter-spacing: 1.5px;
  }
}
@media (max-width: 767px) {
  .advantages {
    padding-top: 50px;
  }
  .advantages__list {
    display: flex;
    flex-direction: column;
    transform: translate(-1.5px, 0);
  }
  .advantages__item {
    min-height: 340px;
    padding: 30px 25px 15px;
  }
  .advantages__item svg {
    width: 30px;
    height: 30px;
  }
  .advantages__item h3 {
    margin: 13px 0 5px;
    font-size: 20px;
  }
  .advantages__item p {
    margin: 3px 0 5px;
    font-size: 14px;
    line-height: 19px;
  }
}
.footer {
  position: relative;
  display: flex;
  min-height: 120px;
  width: 100%;
  padding-top: 45px;
  padding-bottom: 48px;
  background: #041827;
}

.footer__container {
  display: grid;
  grid-template-columns: 1fr 2fr 1.5fr;
  gap: 5vw;
}

.footer__social {
  display: flex;
  flex-direction: column;
  gap: 35px;
  transform: translate(4px, 5px);
}

.footer__social--logo img {
  transition: all 0.5s ease-in-out;
}

.footer__social--logo img:hover,
.footer__social--logo img:focus {
  opacity: 0.8;
  transform: scale3d(1.02, 1.02, 1.02);
}

.footer__social--logo img:active {
  opacity: 0.6;
  transform: none;
}

.footer__social--list {
  display: flex;
  flex-wrap: wrap;
  gap: 25px;
  transform: translate(15px, 0);
}

.footer__social--link svg {
  color: #f8fdff;
  transition: all 0.5s ease-in-out;
}

.footer__social--link svg:hover,
.footer__social--link svg:focus {
  opacity: 0.8;
  transform: scale3d(1.02, 1.02, 1.02);
}

.footer__social--link svg:active {
  color: #00b3ff;
  opacity: 1;
  transform: none;
}

.footer__navigation {
  transform: translate(2.8vw, 0);
}

.footer__navigation h2,
.footer__address h2 {
  margin-bottom: 19px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
  font-size: 15px;
  line-height: 18px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
}

.footer__navigation--container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 42px;
}

.footer__navigation--list,
.footer__address--list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  gap: 5px;
}

.footer__navigation--link {
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: rgba(255, 255, 255, 0.7);
  transition: all 0.5s ease-in-out;
}

.footer__navigation--link:hover,
.footer__navigation--link:focus {
  font-size: 16.2px;
  color: #ffffff;
  opacity: 0.8;
}

.footer__navigation--link:active {
  color: #ffffff;
  opacity: 1;
}

.footer__accordion--content p {
  margin-bottom: 10px;
  font-family: inherit;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: rgba(255, 255, 255, 0.7);
  transform: translate(0, 2px);
}

.footer__address--link {
