  .footer__accordion h2::before,
.footer__accordion h2::after {
    transition: all 0.5s ease-in-out;
    opacity: 0;
  }
  .footer__accordion h2:hover::before,
.footer__accordion h2:hover::after {
    transform: rotate3d(1, 1, 1, 360deg);
